.item{
margin-bottom: 15px;
/* width: 100%; */
display: flex;
align-items: center;
justify-content: space-between;
}

.spanName{
  margin-right: 10px;
  min-width: 80px;
  font-weight: 700;
  font-size: 14px;
  font-style: italic;
  text-align: start;
  text-transform: uppercase;
}

@media screen and (min-width: 800px) {
  .spanName{
    margin-right: 15px;
    min-width: 200px;
    font-weight: 700;
    font-style: italic;
    text-align: start;
    text-transform: uppercase;
  }
}

.spanTel{
  margin-right: 15px;
}

.button{
  padding: 5px 10px;
  border-radius: 5px;
  min-width: 90px;
  border: 1px solid;
  cursor: pointer;
  scale: 1;
  color: #002cbe;
}

.button:hover:not(:disabled){
  scale: 1.1;
  background-color:#f27370;
  color: beige;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.button:disabled{
  cursor:not-allowed;
}

@media screen and (max-width: 450px) {
  .button{
    padding: 5px 5px;
    border-radius: 5px;
    min-width: 60px;
    border: 1px solid;
    cursor: pointer;
    scale: 1;
    color: #002cbe;
  }
}
